<template>
  <div id="form-conn">
    <div class="container">
      <div class="row justify-content-center">
        <div class="col-md-6">
          <div class="card">
            <div class="card-body">
              <form action="accueil" name="Login_Form" class="form-signin">
                <h3 class="form-signin-heading">Connectez-vous !</h3>
                <hr class="colorgraph"><br>

                <input type="text" class="form-control" name="user" placeholder="Nom d'utilisateur" required=""/>
                <br>
                <input type="password" class="form-control" name="pass" placeholder="Mot de passe" required=""/>
                <br>
                <button class="btn btn-lg btn-primary" name="envoi" value="submit" type="Submit">Connexion</button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'form-conn'
}
</script>
<!-- styling for the component -->
<style scoped>
#form-conn {
  text-align: center;
  margin-top: 60px;
}
</style>


